
import { ContentList, AttachFileInfoList, ICM21200112_RES, ICM21200112_REQ_BODY } from "@/shared/type/ICM21200112.type";
import { ICM21200121_RES, ICM21200121_REQ_BODY, ICM21200211_REQ, LIST } from "@/shared/type/ICM21200121.type";
import { ICM21200131_RES, ICM21200131_REQ_BODY } from "@/shared/type/ICM21200131.type";
import { ICM21200141_RES, ICM21200141_REQ_BODY } from "@/shared/type/ICM21200141.type";
import baseTab from "@/shared/components/Tab/BaseTab.vue";
import basePan from "@/shared/components/Tab/BaseTabPan.vue";
import { defineComponent } from "vue";
import { FileSize } from "@/shared/services/FileSizeService";
import { FILE_TYPE } from "@/shared/constants/common.const";
import { modalController } from "@ionic/vue";
import BreadCrumb from "@/shared/components/BreadCrumb/BreadCrumb.vue";
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";
import ModifyCard from "@/shared/components/ModifyCard/ModifyCard.vue";
import { APM20200113_RES } from "@/shared/type/APM20200113.type";
import { screenMode } from "@/shared/type/Approval.type";

const fileSize = new FileSize();

export default defineComponent({
  name: "icm212001sc01",
  components: {
    baseTab,
    basePan,
    BreadCrumb,
    SelectOption,
    ModifyCard,
  },

  data() {
    const headersIcon = [
      { key: "En", name: "common.english" },
      { key: "Km", name: "common.khmer" },
      { key: "Ko", name: "common.korean" },
    ];

    const productDetailsLangDefaultValue = [
      { languageCode: "common.english", productName: "", productDesc: "" },
      { languageCode: "common.khmer", productName: "", productDesc: "" },
      { languageCode: "common.korean", productName: "", productDesc: "" },
    ] as ContentList[];

    const productDetail = {
      productCode: "",
      boardCategoryCode: "",
      contentList: [] as ContentList[],
      attachFileInfoList: [] as AttachFileInfoList[],
      firstRegisterTime: "",
      lastChangeTellerID: "",
      lastChangeTime: "",
      displayYN: "",
      lastChangeDate: "",
      lastChangeBranchCode: "",
      lastChangeTellerName: "",
      firstRegisterTellerName: "",
      deleteBranchCode: "",
      deleteTellerName: "",
      deleteTellerID: "",
      firstRegisterDate: "",
      deleteDate: "",
      firstRegisterTellerID: "",
      firstRegisterBranchCode: "",
      deleteTime: "",
      statusCode: "",
    };

    const productCodeList = [] as LIST[];

    const modifyCard = {
      registerTellerId: "",
      registerTellerName: "",
      registerDate: "",
      registerTime: "",
      updatedTellerId: "",
      updatedTellerName: "",
      updatedDate: "",
      updatedTime: "",
      deleteTellerId: "",
      deleteTellerName: "",
      deleteDate: "",
      deleteTime: "",
    };

    const fileInfo = {
      image: "",
      file: "",
      fileSize: "",
      imgSize: "",
      fileName: "",
      imageName: "",
    };

    return {
      productCodeList,
      headersIcon,
      productDetail,
      isEdit: true,
      uploadInput: "",
      modifyCard,
      fileInfo,
      isInValidInput: false,
      productDetailsLangDefaultValue,
      getFile: null,
      getImage: null,
      isFileEmty: false,
      isImageEmty: false,
      operationMode: 'normal' as screenMode,
      approvalRes: {} as any,
    };
  },

  async created() {
    if (this.approvalType === '') {
      await this.requestProductList();
      if (this.$route.query.type === "register") {
        this.isEdit = false;
      } else {
        await this.requestProductDetail();
      }
    } else {
      await this.requestProductList();

      await this.$approval.getAuthorizationDetail().then(async (authDetail: APM20200113_RES) => {
        let authDetailParsed = JSON.parse(authDetail.body.requestContent)
        this.approvalRes = authDetail.body
        this.operationMode = this.approvalType == 'my-request' ? 'read' : this.approvalType == 'my-approval' ? 'approval' : 'normal'

        if (this.$util.checkServiceIdType(authDetail.body.serviceID) == 'delete') {
          const reqBody = { productCode: authDetailParsed.productCode, } as ICM21200112_REQ_BODY;

          const res = (await this.$request.getRequest("ICM21200112", reqBody, false)) as ICM21200112_RES;

          await this.assignValueToTab(this.convertCommonCode(res?.body.contentList))

          this.assignValueToAttachInfoList(res?.body.attachFileInfoList)

          this.productDetail = {
            deleteTellerID: res.body.deleteTellerID ? res.body.deleteTellerID : "",
            deleteBranchCode: res.body.deleteBranchCode ? res.body.deleteBranchCode : "",
            displayYN: res.body.displayYN,
            lastChangeTellerName: res.body.lastChangeTellerName,
            lastChangeDate: this.$datetime.formatStringToStringDate(res.body.lastChangeDate),
            productCode: res.body.productCode,
            firstRegisterTime: res.body.firstRegisterTime ? this.$datetime.formatStringToTime(res.body.firstRegisterTime) : "",
            lastChangeTime: res.body.lastChangeTime ? this.$datetime.formatStringToTime(res.body.lastChangeTime) : "",
            firstRegisterBranchCode: res.body.firstRegisterBranchCode ? res.body.firstRegisterBranchCode : "",
            lastChangeTellerID: res.body.lastChangeTellerID ? res.body.lastChangeTellerID : "",
            lastChangeBranchCode: res.body.lastChangeBranchCode ? res.body.lastChangeBranchCode : "",
            attachFileInfoList: this.productDetail.attachFileInfoList,
            deleteDate: this.$datetime.formatStringToStringDate(res?.body.deleteDate),
            firstRegisterDate: this.$datetime.formatStringToStringDate(res.body.firstRegisterDate),
            firstRegisterTellerID: res.body.firstRegisterTellerID ? res.body.firstRegisterTellerID : "",
            contentList: res.body.contentList,
            deleteTellerName: res.body.deleteTellerName,
            deleteTime: res.body.deleteTime ? this.$datetime.formatStringToTime(res.body.deleteTime) : "",
            firstRegisterTellerName: res.body.firstRegisterTellerName,
            boardCategoryCode: res.body.boardCategoryCode,
            statusCode: res.body.statusCode,
          };

          await Promise.all([this.requestFileDetail(FILE_TYPE.FILE), this.requestFileDetail(FILE_TYPE.IMAGE)])

        } else {
          await this.assignValueToTab(this.convertCommonCode(authDetailParsed.body.contentList))

          this.assignValueToAttachInfoList(authDetailParsed.body.attachFileInfoList)

          this.productDetail = {
            deleteTellerID: authDetailParsed.deleteTellerID ? authDetailParsed.deleteTellerID : "",
            deleteBranchCode: authDetailParsed.deleteBranchCode ? authDetailParsed.deleteBranchCode : "",
            displayYN: authDetailParsed.displayYN,
            lastChangeTellerName: authDetailParsed.lastChangeTellerName,
            lastChangeDate: this.$datetime.formatStringToStringDate(authDetailParsed.lastChangeDate),
            productCode: authDetailParsed.productCode,
            firstRegisterTime: authDetailParsed.firstRegisterTime ? this.$datetime.formatStringToTime(authDetailParsed.firstRegisterTime) : "",
            lastChangeTime: authDetailParsed.lastChangeTime ? this.$datetime.formatStringToTime(authDetailParsed.lastChangeTime) : "",
            firstRegisterBranchCode: authDetailParsed.firstRegisterBranchCode ? authDetailParsed.firstRegisterBranchCode : "",
            lastChangeTellerID: authDetailParsed.lastChangeTellerID ? authDetailParsed.lastChangeTellerID : "",
            lastChangeBranchCode: authDetailParsed.lastChangeBranchCode ? authDetailParsed.lastChangeBranchCode : "",
            attachFileInfoList: this.productDetail.attachFileInfoList,
            deleteDate: this.$datetime.formatStringToStringDate(authDetailParsed.deleteDate),
            firstRegisterDate: this.$datetime.formatStringToStringDate(authDetailParsed.firstRegisterDate),
            firstRegisterTellerID: authDetailParsed.firstRegisterTellerID ? authDetailParsed.firstRegisterTellerID : "",
            contentList: authDetailParsed.contentList,
            deleteTellerName: authDetailParsed.deleteTellerName,
            deleteTime: authDetailParsed.deleteTime ? this.$datetime.formatStringToTime(authDetailParsed.deleteTime) : "",
            firstRegisterTellerName: authDetailParsed.firstRegisterTellerName,
            boardCategoryCode: authDetailParsed.boardCategoryCode,
            statusCode: authDetailParsed.statusCode,
          };

          await Promise.all([this.requestFileDetail(FILE_TYPE.FILE), this.requestFileDetail(FILE_TYPE.IMAGE)])
        }
      })
    }
  },

  computed: {
    displayName(): string {
      if (this.$route.query.type === "register") {
        return this.$t("icm212001sc01.product_register")
      } else if (this.$route.query.type === "edit" && !this.isEdit) {
        return this.$t("icm212001sc01.product_edit")
      } else if (this.$route.query.type === "edit" && this.isEdit) {
        return this.$t("icm212001sc01.product_edit")
      } else {
        return ''
      }
    },

    displayYN() {
      return this.$store.state.CommonCodeModule.commonCode.YnType
    },

    approvalType(): string {
      return this.$route.query.approvalType ? this.$route.query.approvalType.toString() : ''
    },
  },

  methods: {
    async requestProductDetail() {
      const reqBody = {
        productCode: String(this.$route.query.productCode),
      } as ICM21200112_REQ_BODY;

      const res = (await this.$request.getRequest("ICM21200112", reqBody, false)) as ICM21200112_RES;

      await this.assignValueToTab(this.convertCommonCode(res.body.contentList))

      this.assignValueToAttachInfoList(res.body.attachFileInfoList)

      this.productDetail = {
        deleteTellerID: res.body.deleteTellerID ? res.body.deleteTellerID : "",
        deleteBranchCode: res.body.deleteBranchCode ? res.body.deleteBranchCode : "",
        displayYN: res.body.displayYN,
        lastChangeTellerName: res.body.lastChangeTellerName,
        lastChangeDate: this.$datetime.formatStringToStringDate(res?.body.lastChangeDate),
        productCode: String(this.$route.query.productCode),
        firstRegisterTime: res.body.firstRegisterTime ? this.$datetime.formatStringToTime(res.body.firstRegisterTime) : "",
        lastChangeTime: res.body.lastChangeTime ? this.$datetime.formatStringToTime(res.body.lastChangeTime) : "",
        firstRegisterBranchCode: res.body.firstRegisterBranchCode ? res.body.firstRegisterBranchCode : "",
        lastChangeTellerID: res.body.lastChangeTellerID ? res.body.lastChangeTellerID : "",
        lastChangeBranchCode: res.body.lastChangeBranchCode ? res.body.lastChangeBranchCode : "",
        attachFileInfoList: this.productDetail.attachFileInfoList,
        deleteDate: this.$datetime.formatStringToStringDate(res.body.deleteDate),
        firstRegisterDate: this.$datetime.formatStringToStringDate(res.body.firstRegisterDate),
        firstRegisterTellerID: res.body.firstRegisterTellerID ? res.body.firstRegisterTellerID : "",
        contentList: res.body.contentList,
        deleteTellerName: res.body.deleteTellerName,
        deleteTime: res.body.deleteTime ? this.$datetime.formatStringToTime(res.body.deleteTime) : "",
        firstRegisterTellerName: res.body.firstRegisterTellerName,
        boardCategoryCode: res.body.boardCategoryCode,
        statusCode: res.body.statusCode,
      };

      this.modifyCard = {
        registerTellerId: String(this.productDetail.firstRegisterTellerID),
        registerTellerName: String(this.productDetail.firstRegisterTellerName),
        registerDate: String(this.productDetail.firstRegisterDate),
        registerTime: String(this.productDetail.firstRegisterTime),
        updatedTellerId: this.productDetail.statusCode === '09' ? "" : res?.body.lastChangeTellerID,
        updatedTellerName: this.productDetail.statusCode === '09' ? "" : res?.body.lastChangeTellerName,
        updatedDate: this.productDetail.statusCode === '09' ? "" : res?.body.lastChangeDate,
        updatedTime: this.productDetail.statusCode === '09' ? "" : res?.body.lastChangeTime,
        deleteTellerId: this.productDetail.statusCode === '09' ? res?.body.lastChangeTellerID : "",
        deleteTellerName: this.productDetail.statusCode === '09' ? res?.body.lastChangeTellerName : "",
        deleteDate: this.productDetail.statusCode === '09' ? res?.body.lastChangeDate : "",
        deleteTime: this.productDetail.statusCode === '09' ? res?.body.lastChangeTime : "",
      };

      await Promise.all([this.requestFileDetail(FILE_TYPE.FILE), this.requestFileDetail(FILE_TYPE.IMAGE)])
    },

    async requestFileDetail(attachTypeCode: string) {
      for (let i = 0; i < this.productDetail.attachFileInfoList.length; i++) {
        if (this.productDetail.attachFileInfoList[i].attachTypeCode == attachTypeCode) {
          const res = await this.$file.getFileInfo(this.productDetail.attachFileInfoList[i].fileId)
          if (attachTypeCode == FILE_TYPE.FILE) {
            this.fileInfo.fileName = res?.data.body.originalFileName
            this.fileInfo.fileSize = res?.data.body.fileSizeString
          } else {
            this.fileInfo.imageName = res?.data.body.originalFileName
            this.fileInfo.imgSize = res?.data.body.fileSizeString
            const resImage = await this.$file.previewImage(this.productDetail.attachFileInfoList[i].fileId)
            this.fileInfo.image = resImage
          }
        }
      }
    },

    async requestProductList() {
      const requestBody = {
        searchTypeCode: "",
        searchValue: "",
        pageSize: 1000,
        pageNumber: 1
      } as ICM21200211_REQ

      const res = await this.$request.getRequest("ICM21200211", requestBody, false) as LIST;

      this.productCodeList = res.body.list;
      
      this.productDetail.boardCategoryCode = res.body.list.categoryCode;
    },

    async handleRegister() {
      await this.handleFileRegister()

      const reqBody = {
        boardCategoryCode: this.productDetail.boardCategoryCode,
        displayYN: this.productDetail.displayYN,
        contentList: this.convertLangToCode(JSON.parse(JSON.stringify(this.productDetailsLangDefaultValue))),
        attachFileInfoList: this.productDetail.attachFileInfoList,
      } as ICM21200121_REQ_BODY;

      const res = (await this.$request.getRequest("ICM21200121", reqBody, false)) as ICM21200121_RES;

      if (res.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.register_success"),
          dialogMode: "Success",
          callback: () => {
            modalController.dismiss();
          },
        });
      }

      return res.header.result;
    },

    async handleFileRegister() {
      if (this.getFile !== null && this.getImage !== null) {
        const res = await this.$file.uploadFile([this.getFile, this.getImage], "ICM");
        for (let i = 0; i < res.data.body.fileList.length; i++) {
          if (res.data.body.fileList[i].keyName == "file1") {
            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
            this.productDetail.attachFileInfoList.push({
              filePath: resFileInfo.data.body.filePath,
              attachTypeCode: FILE_TYPE.FILE,
              fileId: res.data.body.fileList[i].fileId
            })
          }
          if (res.data.body.fileList[i].keyName == "file2") {
            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
            this.productDetail.attachFileInfoList.push({
              filePath: resFileInfo.data.body.filePath,
              attachTypeCode: FILE_TYPE.IMAGE,
              fileId: res.data.body.fileList[i].fileId
            })
          }
        }
      } else if (this.getFile !== null && this.getImage === null) {
        const res = await this.$file.uploadFile([this.getFile], 'ICM')
        for (let i = 0; i < res.data.body.fileList.length; i++) {
          const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
          this.productDetail.attachFileInfoList.push({
            filePath: resFileInfo.data.body.filePath,
            attachTypeCode: FILE_TYPE.FILE,
            fileId: res.data.body.fileList[i].fileId
          })
        }
      } else if (this.getFile === null && this.getImage !== null) {
        const res = await this.$file.uploadFile([this.getImage], 'ICM')
        for (let i = 0; i < res.data.body.fileList.length; i++) {
          const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
          this.productDetail.attachFileInfoList.push({
            filePath: resFileInfo.data.body.filePath,
            attachTypeCode: FILE_TYPE.IMAGE,
            fileId: res.data.body.fileList[i].fileId
          })
        }
      }
    },

    async handleEdit() {
      await this.handleFileUpdate()
      const reqBody = {
        boardCategoryCode: this.productDetail.boardCategoryCode,
        displayYN: this.productDetail.displayYN,
        contentList: this.convertLangToCode(JSON.parse(JSON.stringify(this.productDetailsLangDefaultValue))),
        attachInfoList: this.productDetail.attachFileInfoList,
        productCode: this.productDetail.productCode,
      } as ICM21200131_REQ_BODY;

      const res = (await this.$request.getRequest("ICM21200131", reqBody, false)) as ICM21200131_RES;

      if (res.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.update_success"),
          dialogMode: "Success",
          callback: () => {
            this.requestProductDetail();
            modalController.dismiss();
          },
        });
      }

      return res.header.result;
    },

    async handleFileUpdate() {
      if (this.getFile !== null && this.getImage !== null && !this.isFileEmty && !this.isImageEmty) {
        if (this.productDetail.attachFileInfoList) {
          this.productDetail.attachFileInfoList.forEach(async (item) => {
            await this.$file.updateFileStatus(item.fileId, FILE_TYPE.INACTIVE)
          })
        }
        this.productDetail.attachFileInfoList = []
        const res = await this.$file.uploadFile([this.getFile, this.getImage], "ICM");
        for (let i = 0; i < res.data.body.fileList.length; i++) {
          if (res.data.body.fileList[i].keyName === "file1") {
            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
            this.productDetail.attachFileInfoList.push({
              filePath: resFileInfo.data.body.filePath,
              attachTypeCode: FILE_TYPE.FILE,
              fileId: res.data.body.fileList[i].fileId
            })
          }
          if (res.data.body.fileList[i].keyName === "file2") {
            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
            this.productDetail.attachFileInfoList.push({
              filePath: resFileInfo.data.body.filePath,
              attachTypeCode: FILE_TYPE.IMAGE,
              fileId: res.data.body.fileList[i].fileId
            })
          }
        }
      }

      if (this.getFile === null && this.isFileEmty) {
        for (let i = 0; i < this.productDetail.attachFileInfoList.length; i++) {
          if (this.productDetail.attachFileInfoList[i].attachTypeCode === FILE_TYPE.FILE) {
            await this.$file.updateFileStatus(this.productDetail.attachFileInfoList[i].fileId, FILE_TYPE.INACTIVE)
            const index = this.productDetail.attachFileInfoList.indexOf(this.productDetail.attachFileInfoList[i]);
            this.productDetail.attachFileInfoList.splice(index, 1);
          }
        }
      }

      if (this.getImage === null && this.isImageEmty) {
        for (let i = 0; i < this.productDetail.attachFileInfoList.length; i++) {
          if (this.productDetail.attachFileInfoList[i].attachTypeCode === FILE_TYPE.IMAGE) {
            await this.$file.updateFileStatus(this.productDetail.attachFileInfoList[i].fileId, FILE_TYPE.INACTIVE)
            const index = this.productDetail.attachFileInfoList.indexOf(this.productDetail.attachFileInfoList[i]);
            this.productDetail.attachFileInfoList.splice(index, 1);
          }
        }
      }

      if (this.getFile !== null && !this.isFileEmty) {
        if (this.productDetail.attachFileInfoList.length === 0) {
          const res = await this.$file.uploadFile([this.getFile], "ICM")
          for (let i = 0; i < res.data.body.fileList.length; i++) {
            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
            this.productDetail.attachFileInfoList.push({
              filePath: resFileInfo.data.body.filePath,
              attachTypeCode: FILE_TYPE.FILE,
              fileId: res.data.body.fileList[i].fileId
            })
          }
        } else if (this.productDetail.attachFileInfoList.length > 0) {
          if (this.isFileAlreadyExisted(FILE_TYPE.FILE)) {
            const res = await this.$file.uploadFile([this.getFile], "ICM")
            for (let index = 0; index < this.productDetail.attachFileInfoList.length; index++) {
              if (this.productDetail.attachFileInfoList[index].attachTypeCode === FILE_TYPE.FILE) {
                await this.$file.updateFileStatus(this.productDetail.attachFileInfoList[index].fileId, FILE_TYPE.INACTIVE);
                for (let i = 0; i < res.data.body.fileList.length; i++) {
                  await this.$file.getFileInfo(res.data.body.fileList[i].fileId).then(v =>
                    Object.assign(this.productDetail.attachFileInfoList[index], { fileId: res.data.body.fileList[i].fileId, attachTypeCode: FILE_TYPE.FILE, filePath: v.data.body.filePath })
                  )
                }
              }
            }
          } else {
            const res = await this.$file.uploadFile([this.getFile], "ICM")
            for (let i = 0; i < res.data.body.fileList.length; i++) {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.productDetail.attachFileInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.FILE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
          }
        }
      }

      if (this.getImage !== null && !this.isImageEmty) {
        if (this.productDetail.attachFileInfoList.length === 0) {
          const res = await this.$file.uploadFile([this.getImage], "ICM")
          for (let i = 0; i < res.data.body.fileList.length; i++) {
            const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
            this.productDetail.attachFileInfoList.push({
              filePath: resFileInfo.data.body.filePath,
              attachTypeCode: FILE_TYPE.IMAGE,
              fileId: res.data.body.fileList[i].fileId
            })
          }
        } else if (this.productDetail.attachFileInfoList.length > 0) {
          if (this.isFileAlreadyExisted(FILE_TYPE.IMAGE)) {
            const res = await this.$file.uploadFile([this.getImage], "ICM")
            for (let index = 0; index < this.productDetail.attachFileInfoList.length; index++) {
              if (this.productDetail.attachFileInfoList[index].attachTypeCode === FILE_TYPE.IMAGE) {
                await this.$file.updateFileStatus(this.productDetail.attachFileInfoList[index].fileId, FILE_TYPE.INACTIVE);
                for (let i = 0; i < res.data.body.fileList.length; i++) {
                  await this.$file.getFileInfo(res.data.body.fileList[i].fileId).then(v =>
                    Object.assign(this.productDetail.attachFileInfoList[index], { fileId: res.data.body.fileList[i].fileId, attachTypeCode: FILE_TYPE.IMAGE, filePath: v.data.body.filePath })
                  )
                }
              }
            }
          } else {
            const res = await this.$file.uploadFile([this.getImage], "ICM")
            for (let i = 0; i < res.data.body.fileList.length; i++) {
              const resFileInfo = await this.$file.getFileInfo(res.data.body.fileList[i].fileId)
              this.productDetail.attachFileInfoList.push({
                filePath: resFileInfo.data.body.filePath,
                attachTypeCode: FILE_TYPE.IMAGE,
                fileId: res.data.body.fileList[i].fileId
              })
            }
          }
        }
      }
    },


    async handleDelete() {
      const reqBody = {
        productCode: this.$route.query.productCode,
      } as ICM21200141_REQ_BODY;

      const res = (await this.$request.getRequest("ICM21200141", reqBody, false)) as ICM21200141_RES;

      if (res.header.result) {
        this.$dialog.onOpen({
          content: this.$t("message.delete_success"),
          dialogMode: "Success",
          callback: () => {
            modalController.dismiss();
          },
        });
      }

      return res.header.result;
    },

    convertCommonCode(listArr: ContentList[]): ContentList[] {
      const convertLangCode = listArr.map((item: ContentList) => {
        item.languageCode = item.languageCode ? this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode) : "";
        return item;
      });

      return convertLangCode;
    },

    convertLangToCode(listsArr: ContentList[]): ContentList[] {
      const data = listsArr.map((item: ContentList) => {
        item.languageCode = this.$codeConverter.convertValue("LanguageCodeTranslate", item.languageCode, "value");
        return item;
      });

      return data;
    },

    handleUploadImage(event: any) {
      this.isImageEmty = false

      const accectFile = /(\.png)$/i;
      const files = event.target.files;
      const file = files[0];

      if (!accectFile.exec(file.name)) {
        this.$dialog.onOpen({
          content: this.$t('message.only_png_file_can_be_inserted'),
          dialogMode: "Warning",
          callback: () => {
            modalController.dismiss();
          },
        });
      } else if (file.size > 2097152) {
        this.$dialog.onOpen({
          content: this.$t('message.file_size_must_not_exceed_2MB'),
          dialogMode: "Warning",
          callback: () => {
            modalController.dismiss();
          },
        });
      } else {
        this.getImage = file;
        const size = fileSize.countFileSize(file.size);
        this.fileInfo.imgSize = size;
        const reader = new FileReader();
        reader.readAsDataURL(file);
        this.fileInfo.imageName = file.name;
        reader.onload = () => {
          this.fileInfo.image = String(reader.result);
        };
      }
      const duplicatedFile = document.getElementById('btn02 btn_upload') as HTMLInputElement;
      duplicatedFile.value = ''
    },

    removeFile(attachTypeCode: string) {
      switch (attachTypeCode) {
        case FILE_TYPE.FILE:
          this.isFileEmty = true
          this.getFile = null
          this.fileInfo.fileSize = this.fileInfo.fileName = '';
          break;
        case FILE_TYPE.IMAGE:
          this.isImageEmty = true
          this.getImage = null
          this.fileInfo.imageName = this.fileInfo.imgSize = '';
          break;
      }
    },

    handleUploadFile(event: any) {
      this.isFileEmty = false

      const files = event.target.files;
      const file = files[0];

      if (file.size > 2097152) {
        this.$dialog.onOpen({
          content: this.$t('message.file_size_must_not_exceed_2MB'),
          dialogMode: "Warning",
          callback: () => {
            modalController.dismiss()
          },
        });
      } else {
        this.getFile = file;
        const size = fileSize.countFileSize(file.size);
        this.fileInfo.fileSize = size;
        const reader = new FileReader();
        reader.readAsDataURL(file);
        this.fileInfo.fileName = file.name;
      }

      const duplicatedFile = document.getElementById('actual-btn') as HTMLInputElement;
      duplicatedFile.value = ''
    },


    isFileAlreadyExisted(fileContentType: FILE_TYPE.FILE | FILE_TYPE.IMAGE) {
      const isFileAlreadyExisted = this.productDetail.attachFileInfoList.some(obj => Object.values(obj).includes(fileContentType))
      return isFileAlreadyExisted
    },

    async assignValueToTab(contentsList) {
      for (let i = 0; i < contentsList.length; i++) {
        this.productDetailsLangDefaultValue.map((item) => {
          if (item.languageCode === contentsList[i].languageCode) {
            item.productName = contentsList[i].productName;
            item.productDesc = contentsList[i].productDesc;
          }
        })
      }
    },

    assignValueToAttachInfoList(attachInfoList) {
      this.productDetail.attachFileInfoList = attachInfoList.map((item) => {
        return {
          attachTypeCode: item.attachTypeCode,
          fileId: item.fileId,
          filePath: item.filePath
        }
      })
    },

    handleClickAction(actionType: string) {
      switch (actionType) {
        case "delete":
          if (this.$route.query.type == "edit") {
            this.$dialog.onOpen({
              content: this.$t("message.are_you_sure_you_want_to_delete"),
              dialogMode: "Question",
              callback: () => {
                this.handleDelete();
                this.$router.push("/ICM212001SC00");
                modalController.dismiss();
              },
            });
          } else {
            this.$router.push("/ICM212001SC00");
          }
          break;
        case "cancel":
          this.$dialog.onOpen({
            content: `${this.$t("message.cancel_without_saving_changes")}`,
            dialogMode: "Question",
            callback: () => {
              if (this.$route.query.type == "edit") {
                this.fileInfo.fileName = this.fileInfo.fileSize = this.fileInfo.imageName = this.fileInfo.imgSize = ''
                this.isEdit = true;
                this.requestProductDetail();
              } else {
                this.$router.push("/ICM212001SC00");
              }
            },
          });
          break;
        case "save":
          var item: any = this.productDetailsLangDefaultValue.find(
            (data) => data.languageCode === "common.english"
          );
          if (item.productName && item.productDesc) {
            this.$dialog.onOpen({
              content: this.$t("message.would_you_like_to_save"),
              dialogMode: "Question",
              callback: () => {
                if (this.$route.query.type == "edit") {
                  this.handleEdit().then((res: boolean) => {
                    if (res) {
                      this.requestProductDetail();
                      modalController.dismiss();
                      this.isEdit = true;
                    }
                  });
                } else {
                  this.handleRegister().then((res: boolean) => {
                    if (res) {
                      modalController.dismiss();
                      this.$router.push("/ICM212001SC00");
                    }
                  });
                }
              },
            });
          } else {
            this.isInValidInput = true;
            this.$util.scrollToElement('inp_error')
            this.$util.scrollToElement('invalidInput')
          }
          break;
        case "edit":
          this.isEdit = false;
          break;
        default:
          this.$router.push("/ICM212001SC00");
          break;
      }
    },
  },
});
